.message {
    margin-bottom: 24px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.imageAndText {
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
}

.imageAndText img {
    width: 48px;
    height: 48px;
    margin-left: 12px;
    border-radius: 50%;
}

.text {
    position: relative;
    max-width: 600px;
    min-height: 60px;
    text-align: right;
    padding: 8px 13px;
    border-radius: 10px 10px 0 10px;
    background: #06c;
    box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.1), 0 5px 20px 0 rgba(29, 33, 38, 0.03);
    word-wrap: break-word;
}

.text:after {
    content: '';
    position: absolute;
    width: 15px;
    height: 15px;
    bottom: 0;
    right: -15px;
    background: radial-gradient(circle at right bottom, rgba(0, 0, 0, 0) 15px, rgba(0, 102, 204, 100) 0);
    transform: rotate(-90deg);
}

.name {
    font-weight: 600;
    font-size: 18px;
    color: #fff;
}

.messageText {
    font-weight: 400;
    font-size: 16px;
    color: #fff;
}

.time {
    font-weight: 600;
    font-size: 10px;
    color: #000;
    transform: translateX(-55%)
}